:root {
  --font-poppins: "Poppins", sans-serif;
  --transition: .2s;
}

:root {
  --white: hsl(0, 0%, 100%);
  --yellow: hsl(49, 100%, 94%);
  --red: hsl(9, 82%, 57%);
  --blue: hsl(194, 100%, 39%);
  --text: hsl(214, 34%, 27%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  color: var(--text);
  font-family: var(--font-poppins);
  text-align: center;
}
@media (min-width: 71.875em) {
  body {
    text-align: left;
  }
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
  font-weight: 400;
}

p {
  margin: 0;
}

input {
  color: var(--text);
  background-color: transparent;
  padding-left: 1.875rem;
  border-width: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--text);
}

button,
.button {
  cursor: pointer;
  border-width: 0;
  color: var(--white);
  border-radius: 1.25rem;
  padding: 0.875rem 3rem;
  background: linear-gradient(0deg, #eb5238 0%, #eb6f38 100%);
}

.title {
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.text {
  font-size: 0.875rem;
  margin-bottom: 1.0625rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 68.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.overlay {
  width: 100%;
  height: 100vh;
  position: absolute;
  background: linear-gradient(190deg, #fff9df 33%, white 100%);
}

.promo {
  position: relative;
  padding-top: 2rem;
  margin-bottom: 6.25rem;
}
@media (min-width: 71.875em) {
  .promo {
    background: url("../images/background.svg") no-repeat 42vw 22vh/60%;
    padding-top: 4rem;
    overflow: hidden;
    height: 100vh;
    z-index: 1;
  }
}

.header {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 71.875em) {
  .header {
    flex-direction: row;
    margin-bottom: 5.25rem;
  }
}
.header .logo::before {
  content: "Where promises are kept";
  position: relative;
  display: block;
  height: 3.5625rem;
  width: 17.6875rem;
  text-align: center;
  color: var(--text);
  line-height: 3.25rem;
  margin-bottom: 1.25rem;
  background-image: url("../images/tagline.svg");
}
@media (min-width: 71.875em) {
  .header .logo::before {
    left: -3.75rem;
  }
}
.header__promise {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__promise {
    margin: 0;
  }
}
@media (min-width: 71.875em) {
  .header img {
    margin: 0;
  }
}
.header img:first-child {
  margin-bottom: 0.3125rem;
}
.header img:last-child {
  margin-bottom: 1.25rem;
}

.hero {
  display: flex;
  justify-content: space-between;
  flex-direction: column-reverse;
}
@media (min-width: 71.875em) {
  .hero {
    flex-direction: row;
  }
}
.hero__inputs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
.hero__inputs *:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .hero__inputs {
    flex-direction: row;
  }
  .hero__inputs *:not(:last-child) {
    margin: 0;
  }
}
.hero__text {
  margin-bottom: 3rem;
}
.hero__suptitle {
  font-size: 1.25rem;
}
.hero__title {
  font-size: 2.375rem;
}
.hero__input {
  position: relative;
  width: 10.4375rem;
  height: 2.375rem;
}
.hero__input input {
  width: 100%;
}
.hero__input::before {
  content: "";
  display: block;
  position: absolute;
  width: 1.3125rem;
  height: 1.3125rem;
  background-repeat: no-repeat;
  background-position: 0 80%;
}
.hero__input::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.25rem;
  border-radius: 0.125rem;
}
.hero__input:first-child ::placeholder {
  color: var(--red);
}
.hero__input:first-child::before {
  background-image: url("../images/user.svg");
}
.hero__input:first-child::after {
  background-color: var(--red);
}
.hero__input:last-child ::placeholder {
  color: var(--blue);
}
.hero__input:last-child::before {
  background-image: url("../images/email.svg");
}
.hero__input:last-child::after {
  background-color: var(--blue);
}
.hero__textarea {
  width: 28.625rem;
  height: 8.375rem;
  background: url("../images/comment.svg") no-repeat 46% 44%;
  margin-bottom: 3rem;
  display: none;
}
@media (min-width: 71.875em) {
  .hero__textarea {
    display: block;
  }
}
.hero__textarea textarea {
  position: relative;
  bottom: -0.5rem;
  width: 26.125rem;
  height: 5.625rem;
  border-width: 0;
  color: var(--text);
  line-height: 1.6;
  font-size: 0.875rem;
  margin: 1.125rem 1.25rem;
  resize: none;
}
.hero__textarea ::placeholder {
  color: var(--text);
}
.hero__warning {
  max-width: 28rem;
  margin: 0 auto;
}
.hero__warning p {
  position: relative;
  padding: 4rem 0 0;
}
@media (min-width: 71.875em) {
  .hero__warning p {
    padding: 0 0 0 3.25rem;
  }
}
.hero__warning p::before {
  content: "";
  position: absolute;
  top: 0.5rem;
  left: 50%;
  transform: translate(-50%, 0);
  width: 2.125rem;
  height: 2.3125rem;
  background-image: url("../images/shield.svg");
}
@media (min-width: 71.875em) {
  .hero__warning p::before {
    left: 0;
  }
}
.hero__buttons {
  margin-bottom: 3.125rem;
}
.hero__buttons .button {
  display: block;
  padding-right: 3.5rem;
  padding-left: 1.5rem;
  margin: 0 auto 1.25rem;
}
@media (min-width: 71.875em) {
  .hero__buttons .button {
    margin-bottom: 0;
    margin-right: 1.875rem;
    display: inline-block;
  }
}
.hero__buttons a {
  text-decoration: underline;
}
.hero img {
  margin: 0 auto;
  max-width: 50%;
  margin-bottom: 3rem;
}
@media (min-width: 71.875em) {
  .hero img {
    position: relative;
    top: -12.1875rem;
    left: 9.6875rem;
    max-width: 100%;
  }
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 3.5625rem;
}
@media (min-width: 71.875em) {
  .about {
    flex-direction: row;
  }
}
.about__info {
  margin-bottom: 8.4375rem;
}
.about__links {
  display: flex;
  flex-direction: column;
  margin-bottom: 3.5625rem;
}
@media (min-width: 71.875em) {
  .about__links {
    flex-direction: row;
    margin-top: 8.4375rem;
  }
}
.about__links .text {
  padding: 0.75rem 2.3125rem 0.9375rem 1.375rem;
  background-color: var(--yellow);
  margin-bottom: 1.25rem;
  border-radius: 1.25rem;
}
@media (min-width: 71.875em) {
  .about__links .text {
    margin: 0;
  }
  .about__links .text:not(:last-child) {
    margin-right: 1.1875rem;
  }
}
.about__title {
  font-size: 1.875rem;
  color: var(--red);
  margin-bottom: 3.5625rem;
}
.about__subtitle {
  font-size: 1.375rem;
  max-width: 33.4375rem;
}
.about__cards {
  position: relative;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .about__cards {
    margin-right: 8.4375rem;
  }
}
@media (min-width: 71.875em) {
  .about__cards .card:nth-child(2) {
    position: absolute;
    top: 35%;
    right: -75%;
    transform: translate(75%, -35%);
  }
}
.about__cards .card {
  padding: 3.75rem 1.75rem 2.5rem;
  transition: var(--transition);
  border-radius: 1.25rem;
  max-width: 13.1875rem;
  position: relative;
  margin-bottom: 3.75rem;
}
.about__cards .card__image {
  position: absolute;
  top: -1.625rem;
  left: -1.625rem;
  width: 3.875rem;
  height: 3.875rem;
  background-color: var(--red);
  border-radius: 50%;
}
.about__cards .card__image::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--red);
  border-radius: 50%;
  width: 4.75rem;
  height: 4.75rem;
  opacity: 0.36;
}
.about__cards .card__image svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: var(--white);
}
.about__cards .card:hover {
  background: linear-gradient(0deg, #eb5238 0%, #eb6f38 100%);
  box-shadow: 0.3125rem 0.3125rem 1.875rem var(--red);
  color: var(--white);
}
.about__cards .card:hover .card__image {
  background-color: var(--white);
}
.about__cards .card:hover .card__image::before {
  background-color: var(--white);
}
.about__cards .card:hover .card__image svg {
  fill: var(--text);
}
.about__cards .card__link {
  position: relative;
  color: var(--white);
  padding-right: 3.3125rem;
}
.about__cards .card__link::after {
  content: "";
  position: absolute;
  top: 0.125rem;
  right: 0;
  display: block;
  width: 1.125rem;
  height: 1.25rem;
  background-image: url("../images/play.svg");
}

.subscribe {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 71.875em) {
  .subscribe {
    flex-direction: row;
  }
}
.subscribe__content {
  display: flex;
  flex-direction: column;
  margin-bottom: 6.25rem;
}
@media (min-width: 71.875em) {
  .subscribe__content {
    flex-direction: row;
    margin-bottom: 10.125rem;
  }
}
.subscribe__content img {
  margin: 0 auto 2rem;
  max-width: 7.5rem;
}
@media (min-width: 71.875em) {
  .subscribe__content img {
    margin-right: 5rem;
    margin-bottom: 0;
  }
}
.subscribe__block {
  max-width: 37.5rem;
}
.subscribe__block:last-child {
  max-width: 22.5rem;
}
.subscribe__input {
  padding: 1rem 0;
  margin-right: 1rem;
  padding-left: 1.625rem;
  border-radius: 1.25rem;
  background: linear-gradient(190deg, white 33%, #fff9df 100%);
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .subscribe__input {
    margin-bottom: 0;
  }
}
.subscribe__block:last-child .button {
  background: var(--blue);
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 71.875em) {
  .footer {
    flex-direction: row;
  }
}
.footer__info {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 71.875em) {
  .footer__info {
    flex-direction: row;
  }
}
.footer__info p {
  margin-bottom: 2.25rem;
}
@media (min-width: 71.875em) {
  .footer__info p {
    margin: 0;
  }
}
.footer a {
  display: block;
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .footer a {
    display: inline-block;
    margin: 0;
  }
}
.footer a:not(:last-child) {
  margin-right: 0;
}
@media (min-width: 71.875em) {
  .footer a:not(:last-child) {
    margin-right: 2.25rem;
  }
}